#square {
  width: 15px;
  height: 15px;
  outline: 1.4px solid rgb(53, 53, 53);
  border-radius: 3px;
  background-color: grey;
  margin: .3em;
}

.single-day {
  position: relative;
  display: inline-block;
}


.single-day:hover .tooltiptext{
  visibility: visible;
}

/* Tooltip text */
.single-day .tooltiptext {
  visibility: hidden;
  background-color: black;
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
 
  position: absolute;
  z-index: 1;
}

